.avatar{
    width: 40px;
    height: 40px;
    position: relative;
    overflow: hidden;
    &.rounded{
        border-radius: 50%;
    }

    & img{
        width: 100%;
        height: 100%;
    }

    & .avatar-name{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: white;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: bold;
    }
}