@use '../../Themes/color';

.input{
    display: block;
    width: 400px;
    padding-left: 1rem;
    padding-right: 1rem;
    // border-radius: 0.25rem;
    border : 1px solid;
    border-color: inherit;
    background-color: color.$white;

    &.sm{
        height: 40px;
        font-size: 0.875rem;
    }
    &.md{
        height: 56px;
        font-size: 1rem;
    }
    &.lg{
        height: 64px;
        font-size: 1.25rem;
    }
}