.search-box{
    display: flex;
    align-items: center;
    justify-content: center;

    & .flat-btn{
        width: 60px;
        height: 42px;
        border: 1px solid rgba(0,0,0,.5);
        background: white;
        // padding: 8px 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        font-size: 16px;
        cursor: pointer;
        
        &:hover{
            background: rgba(0,0,0,.05) ;
        }
    }

    & input{
        border : 1px solid rgba(0, 0, 0, 0.5);
        outline: none;
    }

    & input::placeholder {
        font-size: 16px;
        font-weight: 300;
    }
}